.stores {
  // flex stuff
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.store {
  background: $white;
  box-shadow: $shad;
  margin: 10px 0;
  width: 100%;

  @include atSmall {
    width: 48%;
  }

  @include atMedium {
    width: 48%;
  }

  @include atLarge {
    width: 30%;
  }
}

.store__hero {
  // positioning
  position: relative;

  padding: 0 10px 0 10px;

  &:before {
    // positioning
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;

    clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%);
    content: '';
    display: block;
    height: 100%;
    opacity: 0.6;
    width: 100%;
  }

  img {
    // positioning
    position: absolute;
    top: 0;
    right: 0;
    left: 0;

    clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%);
    height: 100%;
    object-fit: cover;
    width: 100%;
  }
}

