@include atSmall {
  width: 48%;
}

.store__actions {
  position: relative;
  z-index: 2;

  // flex stuff
  display: flex;
  align-items: center;
  justify-content: space-around;

  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2), 0 1px 0 rgba(255, 255, 255, 0.2);
  color: $white;
  margin-bottom: 50px;
  padding-bottom: 20px;
  padding-top: 10px;
}

.store__action {
  font-size: 10px;
  svg {
    width: 25px;
    fill: $white;
  }
  &--edit {
    a {
      border-bottom: 0;
    }
  }
}



